<template>
  <div class="project-item">
    <my-image class="my-img fade" :imgUrl="path+data.icon" @click.native="clickDetailsHandle"></my-image>
    <div class="project-item-content">
      <div class="left">
        <p class="name">{{data.gname}}</p>
        <p class="slogan">{{data.message}}</p>
      </div>
      <div class="right">
        <mt-button size="small" class="btn info" type="default" @click.native="clickDetailsHandle">查看商品详情</mt-button>
      </div>
    </div>
  </div>
</template>

<script>
import myImage from "../../lazyloadImg/lazyImage.vue";
import Vue from "vue";
import { Toast, Button } from "mint-ui";
import "mint-ui/lib/button/style.css";
import "mint-ui/lib/toast/style.css";
Vue.component(Button.name, Button);
Vue.component(Toast.name, Toast);
import { mapState } from "vuex";


export default {
  name: "project-item",
  props: {
    data: {
      type: Object
    }
  },
  components: {
    myImage
  },
  created() {
  },
  methods: {
    clickDetailsHandle(event) {
      this.$store.commit("UPDATE_CURMEALDATA", {"cardId":this.data.id});
      this.$router.push({ name: "details" });
    }
  },
  computed: {
    ...mapState(["path"])
  }
};
</script>
<style lang="stylus" rel="stylesheet/stylus">
.project-item {
  background: #fefefe;
  padding: 0.1rem 0.1rem 0 0.1rem;
  box-sizing: border-box;

  .my-img {
    width: 100%;
    height: 1.4rem;
    background-color: #f1f1f1;
  }

  .project-item-content {
    width: 100%;
    height: 0.5rem;
    color: #000;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .left {
      .name {
        font-size: 0.14rem;
        font-weight: 600;
        letter-spacing: 0.005rem;
      }

      .slogan {
        margin-top: 0.04rem;
        font-size: 0.13rem;
        color: #898989;
        letter-spacing: 0.005rem;
      }
    }

    .right {
      margin-top 0.02rem;
      flex-shrink 0;
      .btn {
        font-size: 0.12rem;
        height: 0.26rem;
        box-shadow: none;
        letter-spacing: 0.005rem;

        &.info {
          margin-right: 0.03rem;
          background-color: #dcdddd;
          color: #a4a5a5;
        }
      }
    }
  }
}
</style>



